@charset "UTF-8";
/** Media Queries **/
/* Globales
----------------------------------------------------------*/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "futura-pt", sans-serif;
  background-color: #fdfaf6;
  font-size: 1.6rem;
  overflow-x: hidden;
}

[class$=contenedor],
.contenedor {
  width: 90%;
  max-width: 96%;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
  width: 100%;
  height: auto;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.seccion {
  padding: 8rem 0;
}

@keyframes anim1 {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.anim {
  opacity: 0;
}

@keyframes anim2 {
  from {
    transform: translateY(-35px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
  font-family: "Forum", cursive;
  font-weight: 400;
}

h1 {
  font-size: 4rem;
}
@media (min-width: 1024px) {
  h1 {
    font-size: 5rem;
  }
}

h2 {
  font-size: 3.5rem;
}
@media (min-width: 1024px) {
  h2 {
    font-size: 4rem;
  }
}

h3 {
  font-size: 2.4rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  font-weight: 300;
  cursor: pointer;
  text-decoration: none;
  font-family: "futura-pt", sans-serif;
}

p {
  font-size: 2rem;
}

/* Botones
----------------------------------------------------------*/
/* Header
----------------------------------------------------------*/
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 96%;
  max-width: 96%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header {
    padding: 1rem 0;
    width: 96%;
    max-width: 96%;
    margin: 0 auto;
  }
}
.header__link {
  padding: 0.8rem;
}
.header__logo {
  width: 6rem;
  display: inline;
}
@media (min-width: 768px) {
  .header__logo {
    width: 7rem;
    padding-bottom: 3rem;
    margin: 0 auto;
    padding-bottom: unset;
    display: block;
  }
}
@media (min-width: 1024px) {
  .header__logo {
    width: 8rem;
  }
}
.header__iconMenu {
  -webkit-tap-highlight-color: transparent;
  outline: none;
}
@media (min-width: 768px) {
  .header__iconMenu {
    display: none;
  }
}
.header__icon {
  width: 3rem;
}

/* Sociales
----------------------------------------------------------*/
.sociales {
  padding: 1rem 0;
  display: flex;
  justify-content: center;
  gap: 1.7rem;
}
.sociales__enlace::before {
  content: "";
  padding: 1.5rem;
  display: inline-block;
}
.sociales__enlace[href*="facebook.com"]::before {
  background-image: url(../img/icono_facebook.svg);
}
.sociales__enlace[href*="twitter.com"]::before {
  background-image: url(../img/icono_twitter.svg);
}
.sociales__enlace[href*="instagram.com"]::before {
  background-image: url(../img/icono_instagram.svg);
}
.sociales__enlace[href*="youtube.com"]::before {
  background-image: url(../img/icono_youtube.svg);
}
.sociales__enlace[href*="tiktok.com"]::before {
  background-image: url(../img/icono_tiktok.svg);
}
.sociales__accesible {
  overflow: hidden;
  width: 1px;
  height: 1px;
  margin: -1px;
  display: block;
}

.navegacion {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  background-color: #fdfaf6;
  position: absolute;
  left: 0;
  right: 0;
  top: 84px;
  bottom: 0;
  z-index: 3;
  height: 0;
  overflow: hidden;
  transition: opacity 0.35s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  opacity: 0;
}
@media (min-width: 768px) {
  .navegacion {
    height: unset;
    flex-direction: row;
    gap: 2rem;
    position: unset;
    opacity: 1;
  }
}
@media (min-width: 1024px) {
  .navegacion {
    gap: 2.5rem;
  }
}
.navegacion__enlace {
  font-size: 3rem;
  color: #595959;
  padding-bottom: 0.3rem;
}
@media (min-width: 768px) {
  .navegacion__enlace {
    font-size: 1.5rem;
    font-size: 1.4rem;
    text-transform: uppercase;
    letter-spacing: 2px;
  }
}
.navegacion__enlace:nth-child(4) {
  margin-bottom: 10rem;
}
@media (min-width: 768px) {
  .navegacion__enlace:nth-child(4) {
    margin-bottom: 0;
  }
}
.navegacion__enlace:last-of-type {
  letter-spacing: 3px;
  color: #d2bab2;
  font-size: 3rem;
  border-bottom: 1px solid #c9aca2;
  padding: 1rem 4rem;
  transition: all 0.3s ease;
  position: absolute;
  bottom: 22px;
}
.navegacion__enlace:last-of-type:hover {
  background-color: #c9aca2;
  color: #FFFFFF;
  border: none;
}
@media (min-width: 768px) {
  .navegacion__enlace:last-of-type {
    position: unset;
    font-size: 1.75rem;
    padding: 1.3rem 2rem;
    margin-left: 1rem;
  }
}
.navegacion--activo {
  height: calc(100% - 84px);
  opacity: 1;
}

.testimoniales {
  margin-bottom: 5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .testimoniales {
    margin-bottom: 8rem;
  }
}
.testimoniales__heading {
  color: #c9aca2;
  line-height: 1;
}

.swiper {
  width: 85%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .swiper {
    width: 82%;
  }
}
@media (min-width: 1440px) {
  .swiper {
    width: 110rem;
  }
}
.swiper .swiper-wrapper {
  cursor: grab;
}
.swiper .swiper-wrapper:active {
  cursor: grabbing;
}
.swiper .swiper-button-prev::after,
.swiper .swiper-button-next::after {
  color: #000;
  display: inline-block;
  font-size: 1.8rem;
  color: #595959;
}
.swiper .swiper-pagination-bullet {
  background-color: #FFFFFF;
  display: none;
}
.swiper .swiper-pagination-bullet-active {
  background-color: #FFFFFF;
}
.swiper .swiper-slide {
  background-color: #fdfaf6;
  height: 290px;
  height: 370px;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .swiper .swiper-slide {
    height: 18rem;
  }
}
.swiper .swiper-contenedor {
  text-align: center;
}
.swiper .swiper-button-next {
  top: 15px;
}
@media (min-width: 1024px) {
  .swiper .swiper-button-next {
    right: 50px;
  }
}
.swiper .swiper-button-prev {
  top: 15px;
  left: unset;
  right: 40px;
}
@media (min-width: 1024px) {
  .swiper .swiper-button-prev {
    right: 80px;
  }
}
.swiper .swiper-text {
  color: #595959;
  line-height: 1.5;
}

/* Bienvenida
----------------------------------------------------------*/
.bienvenida {
  text-align: center;
  background-image: url(../img/hero.jpg);
  background-position: center 30%;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
.bienvenida::before {
  content: "";
  background-color: rgba(253, 250, 246, 0.1882352941);
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
}
.bienvenida__contenido {
  position: relative;
  padding: 15rem 0;
}
@media (min-width: 768px) {
  .bienvenida__contenido {
    padding: 20rem 0;
  }
}
@media (min-width: 1024px) {
  .bienvenida__contenido {
    padding: 28rem 0;
  }
}
.bienvenida__heading {
  text-transform: uppercase;
  line-height: 1.3;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .bienvenida__heading {
    width: 80%;
    margin: 0 auto;
  }
}
@media (min-width: 1024px) {
  .bienvenida__heading {
    width: 50%;
  }
}
.bienvenida__texto {
  font-size: 1rem;
  max-width: 50rem;
  margin: 0 auto;
  line-height: 1.8;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: #595959;
  margin-bottom: 4rem;
}
@media (min-width: 768px) {
  .bienvenida__texto {
    font-size: 1.2rem;
  }
}
.bienvenida__enlace {
  letter-spacing: 1.8px;
  margin-top: 2rem;
  font-size: 2.5rem;
  padding: 0.8rem 6rem;
  transition: all 0.3s ease;
  background-color: #c9aca2;
  color: #FFFFFF;
}
.bienvenida__enlace:hover {
  background-color: #b68f82;
}

/* Bienvenida
----------------------------------------------------------*/
.servicios {
  padding: 5rem 0 2rem 0;
}
@media (min-width: 768px) {
  .servicios {
    padding: 8rem 0 4rem 0;
  }
}
.servicios__heading {
  text-align: center;
  line-height: 1;
}
.servicios__texto {
  text-align: center;
  color: #c9aca2;
  text-transform: uppercase;
  margin-bottom: 7rem;
  letter-spacing: 2px;
  font-size: 1.5rem;
  line-height: 1.5;
}
@media (min-width: 1024px) {
  .servicios__texto {
    font-size: 1.7rem;
    margin-bottom: 10rem;
  }
}
.servicios__grid {
  width: 85%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .servicios__grid {
    width: 100%;
    margin: unset;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .servicios__grid {
    width: 80%;
    margin: 0 auto;
    gap: 5rem;
  }
}

.card__imgContainer {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  transition-property: rotate scale;
  transition-duration: 300ms;
  aspect-ratio: 1/1;
}
.card__imgContainer:hover {
  transform: scale(1.1) rotate(7deg);
}
.card__imgContainer::before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgba(253, 250, 246, 0.1882352941);
  position: absolute;
}
.card__imgContainer--1 {
  background-image: url(../img/servicio_1.jpg);
}
.card__imgContainer--2 {
  background-image: url(../img/servicio_2.jpg);
}
.card__imgContainer--3 {
  background-image: url(../img/servicio_3.png);
}
.card__info {
  max-width: 90%;
  margin: 0 auto;
  background-color: #FFFFFF;
  text-align: center;
  transform: translateY(-5rem);
}
.card__heading {
  color: #c9aca2;
  font-family: "Italianno", sans-serif;
  line-height: 0.8;
  font-weight: 400;
  text-transform: lowercase;
  margin-bottom: 4rem;
  font-size: 4rem;
  padding: 2rem 0;
  margin: 0;
  font-size: 3.5rem;
}
.card__heading > * {
  display: block;
  font-size: 3.5rem;
  text-transform: uppercase;
  color: initial;
  font-family: "Forum", cursive;
}
.card__span {
  font-size: 2.5rem;
}
.card__btn {
  letter-spacing: 1.8px;
  margin-top: 2rem;
  font-size: 2.5rem;
  padding: 0.8rem 6rem;
  transition: all 0.3s ease;
  background-color: #c9aca2;
  color: #FFFFFF;
  display: block;
  padding: 1rem 0;
  margin-top: 1rem;
}
.card__btn:hover {
  background-color: #b68f82;
}

/* Horarios
----------------------------------------------------------*/
@media (min-width: 768px) {
  .conocenos {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
.conocenos__info {
  background-color: #c9aca2;
  padding: 3rem 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .conocenos__info {
    padding: 5rem;
  }
}
.conocenos__heading {
  text-transform: uppercase;
  text-align: center;
}
.conocenos__descripcion {
  text-align: center;
  font-size: 2rem;
  line-height: 2;
}
.conocenos__img {
  height: 35rem;
  background-image: url(../img/salon.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: right center;
  position: relative;
}
@media (min-width: 768px) {
  .conocenos__img {
    display: block;
    height: auto;
    background-position: center;
  }
}
@media (min-width: 1024px) {
  .conocenos__img {
    background-position: right center;
    height: 60rem;
  }
}
.conocenos__img::before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgba(253, 250, 246, 0.1882352941);
  position: absolute;
}

.reserva {
  background-color: #5e756e;
}
.reserva__contenido {
  min-height: 35rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.reserva__heading {
  margin: 0;
  text-align: center;
  color: #FFFFFF;
  font-size: 4rem;
}
@media (min-width: 768px) {
  .reserva__heading {
    font-size: 5rem;
  }
}
@media (min-width: 1024px) {
  .reserva__heading {
    font-size: 5.5rem;
  }
}
.reserva__enlace {
  letter-spacing: 1.8px;
  margin-top: 2rem;
  font-size: 2.5rem;
  padding: 0.8rem 6rem;
  transition: all 0.3s ease;
  background-color: #FFFFFF;
  color: #FFFFFF;
  color: #000;
}
.reserva__enlace:hover {
  background-color: #e6e6e6;
}

.galeria {
  width: 90%;
  margin: 0 auto;
}
.galeria__grid {
  display: grid;
  gap: 0.1rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .galeria__grid {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (min-width: 1024px) {
  .galeria__grid {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(6, 1fr);
  }
}
.galeria__img {
  height: 16rem;
  object-fit: cover;
}
@media (min-width: 768px) {
  .galeria__img {
    height: 20rem;
  }
}
@media (min-width: 1024px) {
  .galeria__img {
    height: 28rem;
  }
}

/* Productos
----------------------------------------------------------*/
.productos {
  padding: 7rem 0;
}
@media (min-width: 768px) {
  .productos {
    width: 90%;
    padding: 8rem 0;
  }
}
@media (min-width: 1024px) {
  .productos {
    width: 80%;
  }
}
.productos__heading {
  text-align: center;
  line-height: 1;
  margin-top: 0;
}
.productos__texto {
  text-align: center;
  color: #c9aca2;
  text-transform: uppercase;
  margin-bottom: 7rem;
  letter-spacing: 2px;
  font-size: 1.5rem;
  line-height: 1.5;
}
@media (min-width: 1024px) {
  .productos__texto {
    font-size: 1.7rem;
    margin-bottom: 10rem;
  }
}
.productos__grid {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .productos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .productos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.producto__imgContainer {
  overflow: hidden;
}
.producto__img {
  transition-property: transform;
  transition-duration: 250ms;
  height: 15rem;
  object-fit: cover;
}
@media (min-width: 768px) {
  .producto__img {
    height: unset;
  }
}
.producto__img:hover {
  transform: scale(1.2);
}
.producto__info {
  background-color: #FFFFFF;
  padding: 2rem;
}
.producto__heading {
  color: #c9aca2;
  text-align: center;
  margin-top: 0;
  font-size: 2.6rem;
}
@media (min-width: 768px) {
  .producto__heading {
    font-size: 3rem;
  }
}
.producto__description {
  text-transform: uppercase;
  font-size: 1.4rem;
  line-height: 1.8;
  text-align: center;
}
@media (min-width: 768px) {
  .producto__description {
    font-size: 1.5rem;
  }
}
.producto__precio {
  text-align: center;
  font-size: 2rem;
  margin: 0;
  color: #c9aca2;
}
@media (min-width: 768px) {
  .producto__precio {
    font-size: 2.8rem;
  }
}

/* Footer
----------------------------------------------------------*/
.footer {
  background-color: #c9aca2;
  color: #FFFFFF;
}
.footer__grid {
  padding: 5rem 0 2rem 0;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1024px) {
  .footer__grid {
    width: 80%;
  }
}
.footer__seccion {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .footer__seccion {
    margin-bottom: 0;
  }
}
.footer__heading {
  font-size: 4rem;
  margin: 0 0 1rem 0;
}
.footer__texto {
  line-height: 1.8;
  text-align: center;
  margin: 0 0 2rem 0;
  font-size: 1.8rem;
}
.footer__iframeContainer {
  text-align: center;
  width: 80%;
  background-color: #ba9588;
  padding: 1.5rem;
  margin: 0 auto;
  margin-bottom: 2rem;
  height: 35rem;
}
@media (min-width: 768px) {
  .footer__iframeContainer {
    height: 25rem;
    width: 100%;
  }
}
.footer__iframe {
  width: 100%;
  height: 100%;
}

.copyright {
  text-align: center;
  border-top: 1px solid #dcc9c2;
  width: 90%;
  margin: 0 auto;
}
.copyright__texto {
  padding: 2rem 0;
  margin: 0;
  font-size: 1.8rem;
}

.anuncio {
  background-color: #c9aca2;
  margin: 0;
  text-align: center;
  color: #FFFFFF;
  font-size: 1.5rem;
  padding: 1.2rem;
  letter-spacing: 0.3px;
}
@media (min-width: 1024px) {
  .anuncio {
    font-size: 1.7rem;
  }
}

.banner {
  position: relative;
}
.banner .banner__img {
  padding-bottom: 120%;
  background-size: cover;
  background-position: 70% center;
}
@media (min-width: 480px) {
  .banner .banner__img {
    padding-bottom: 60%;
    background-position: right 39%;
  }
}
@media (min-width: 1024px) {
  .banner .banner__img {
    min-height: 50rem;
    padding-bottom: 37%;
  }
}
.banner .banner__img--uñas {
  background-image: linear-gradient(to top, rgba(201, 172, 162, 0.7) 0%, rgba(0, 0, 0, 0) 100%), url(../img/manicure-banner2.jpg);
}
.banner .banner__img--pestañas {
  background-image: linear-gradient(to top, rgba(201, 172, 162, 0.7) 0%, rgba(0, 0, 0, 0) 100%), url(../img/eyelash-banner.avif);
}
.banner .banner__img--eyebrows {
  background-image: linear-gradient(to top, rgba(201, 172, 162, 0.7) 0%, rgba(0, 0, 0, 0) 100%), url(../img/eyebrows-banner.avif);
}
.banner .banner__img--nosotros {
  background-image: linear-gradient(to top, rgba(201, 172, 162, 0.7) 0%, rgba(0, 0, 0, 0) 100%), url(../img/salon.jpg);
}
.banner__heading {
  position: absolute;
  bottom: 0;
  margin-bottom: 4rem;
  color: #FFFFFF;
  right: 0;
  left: 0;
  text-align: center;
  font-size: 4rem;
}
@media (min-width: 768px) {
  .banner__heading {
    font-size: 6rem;
  }
}
@media (min-width: 1024px) {
  .banner__heading {
    font-size: 8rem;
  }
}

.listado-servicios {
  margin-block: 5rem;
}
.listado-servicios__servicio {
  border-bottom: 1px solid gray;
  padding: 5rem 0 6rem 0;
}
.listado-servicios__servicio:last-of-type {
  border-bottom: none;
}
@media (min-width: 1024px) {
  .listado-servicios__servicio {
    display: flex;
    gap: 1rem;
    justify-content: space-between;
  }
}
.listado-servicios__header {
  flex: 0.8;
}
.listado-servicios__heading {
  text-transform: uppercase;
  font-size: 2.8rem;
  letter-spacing: 1.6px;
  line-height: 1.5;
  margin: 0 0 1.3rem 0;
}
@media (min-width: 1024px) {
  .listado-servicios__heading {
    font-size: 3rem;
  }
}
.listado-servicios__precio {
  margin: 0 0 5rem 0;
  font-family: "Forum", cursive;
