@use 'bases/variables' as v; 
@use 'bases/mixins' as m;

.equipo{
    margin-block: 8rem;
    width: 75%;
    @include m.desktop{
        width: 85rem;

    }
    .equipos__heading{
        text-align: center;
        // color: v.$beige;
        letter-spacing: 2px;
    }
    .equipo__integrante{
        border-bottom: 1px solid lighten(v.$negro, 50%);
        padding: 5rem 0 6rem 0;


        &:last-of-type {
            border-bottom: none;
        }
    }
    .equipo__nombre{
        color: v.$beige;
        text-transform: uppercase;
        font-size: 2rem;
        letter-spacing: 1.6px;
        line-height: 1.5;
        margin: 0 0 1.3rem 0;
    }
    .equipo__texto{
        flex: 2;
        line-height: 1.5;
        letter-spacing: 1.6px;
        margin: 0; 
        font-size: 1.7rem;
    }

}